import React, { useState } from 'react';
import axios from "axios";

const Contact = ( ) => {


  const[data, saeData] = useState({
    fullname: '' ,
    phone: '' ,
    email: '' ,
    msg: '' ,
  })

  const InputEvent = ( event ) => {
    const { name, value } = event.target;

    saeData (( preVal ) => {
      return {
        ...preVal,
        [name] : value,
      }
    })
  }

  const formSubmit = ( e ) => {
    
    e.preventDefault();
    alert(
      "Form Submitted Successfully."
    );
    axios.post("http://localhost:8080/PostForm", data)
    .then(res => {
      window.location = ('/')
    })
  
  } ;

    return (
      <>
        <div className="my-5">
          <h1 className="text-center"> Contact US </h1>
        </div>

      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">

            <form onSubmit={formSubmit}>
              <div className="mb-3">
                <label form="exampleFormControlInput1" className="form-label">
                  FullName
                </label>
                <input
                  autoComplete="off"
                  type="text"
                  className="form-control"
                  id="exampleFormControlInput1"
                  name="fullname"
                  value={data.fullname}
                  onChange={InputEvent}
                  placeholder="Enter your name"
                />
              </div>

              <div className="mb-3">
                <label form="exampleFormControlInput1" className="form-label">
                  Phone
                </label>
                <input
                  autoComplete="off"
                  type="number"
                  className="form-control"
                  id="exampleFormControlInput1"
                  name="phone"
                  value={data.phone}
                  onChange={InputEvent}
                  placeholder="mobile number"
                />
              </div>

              <div className="mb-3">
                <label form="exampleFormControlInput1" className="form-label">
                  Email address
                </label>
                <input
                  autoComplete="off"
                  type="email"
                  className="form-control"
                  id="exampleFormControlInput1"
                  name="email"
                  value={data.email}
                  onChange={InputEvent}
                  placeholder="name@example.com"
                />
              </div>

              <div className="mb-3">
                <label form="exampleFormControlTextarea1" className="form-label">
                  Message
                </label>
                <textarea
                  className="form-control"
                  id="exampleFormControlTextarea1"
                  rows="3"
                  name="msg"
                  value={data.msg}
                  onChange={InputEvent}
                ></textarea>
              </div>

              <div className="col-12">
                <button className="btn btn-outline-primary" type="submit">
                  Submit form
                </button>
              </div>
            </form>

          </div>
        </div>
      </div>
      </>
    );
  };
  
export default Contact;